.navbar {
  background: #05b6e2f3;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.navbar_logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.nav_menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  align-items: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav_item {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav_links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.nav_links:hover {
  background-color: #1888ff;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
}

.nav_links_mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .navbar {
    position: relative;
  }

  .nav_menu {
    display: flex;
    flex-direction: column;
    width: 30%;
    height: 20rem;
    position: absolute;
    top: 100%;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    align-items: initial;
  }

  .nav_menu.active {
    background: rgba(242, 242, 245, 0.281);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav_links {
    text-align: center;
    padding: 0.5rem;
    width: 100%;
    display: table;
  }

  .nav_links:hover {
    background-color: #1888ff8c;
    border-radius: 0;
  }

  .navbar_logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav_links_mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #1874ffb9;
    text-decoration: none;
    color: #fff;
    font-size: 1rem;
  }

  .nav_links_mobile:hover {
    background: #fff;
    color: #1888ff;
    transition: 250ms;
  }

  button {
    display: none;
  }
}
